@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@300&display=swap);
body {
  margin: 0;
  padding: 0;
  width: 100%;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}


.homeBG {
    float: right;
}
.buttons {
    width: 100%;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-align-items: center;
            align-items: center;
    -webkit-justify-content: center;
            justify-content: center;
}
.signText {
    color: #000000A3;
    font-size: 10px;
}
.signIn {
    background: #FC5317;
    width: 242px;
    height: 47px;
    margin-top: 30px;
    border: 1px solid #fff;
    border-radius: 10px;
    color: #fff;
}
.signUp {
    background: #2958E5;
    width: 242px;
    color: #fff;
    border: 1px solid #fff;
    border-radius: 10px;
    height: 47px;
    margin-top: 20px;
}
.socialIcons {
    margin: 50px 10px;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: row;
            flex-direction: row;
    -webkit-justify-content: space-around;
            justify-content: space-around;
}
.landing {
    background: #FA4A0C;
    height: 100vh;
}
.landingBG {
    margin-left: 0;
}
.landingText {
    margin-left: 5%;
    color: #fff;
    font-size: 36px;
    line-height: 62.5px;
    font-family: 'Poppins', sans-serif;
}
.logo {
    color: #fff;
    margin-left: 5%;
    border: 1px solid #fff;
    border-radius: 20px;
    box-sizing: border-box;
    width: 116px;
    height: 103px;
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
}
.logoText {
    line-height: 126px;
    letter-spacing: -0.025em;
    font-weight: 300;
    font-size: 96px;
    font-family: 'Poppins', sans-serif;
}
.getStarted {
    width: 100%;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
}
.btn-GetStarted {
    background: #2958E5;
    width: 242px;
    color: #fff;
    border: 1px solid #fff;
    border-radius: 10px;
    height: 47px;
    margin-top: 20px;
}
